.loading {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10000;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

#loading-image {
  max-width: 70px;
}

.list-url {
  min-width: 90%;
}

.pi-icon {
  width: 20px;
  display: inline;
}

.is-pro,
.is-pro label {
  color: #f00 !important;
}

.card {
  width: 100%;
  padding: 0px;
  max-width: 100%;
}

